export async function errorHandler() {
  const { fetch } = window;
  window.fetchAuth = async (...args) => {
    let [link, config] = args;
    let response = await fetch(link, config);
    if (!response.ok) {
      console.error(`: ${response.status}`);
      response = await fetch(link, {
        ...config,
      });
    }
    return response;
  };
}
